/* global AFRAME THREE TWEEN PROJECT_VERSION PROJECT_NAME PROJECT_ISSUE_URL */
/**
 * 1. app styles will be extraxted to style/app-theme.css
 *    and not included in final bundle of this entry point!
 */
import './modules/formSubmit.js'
import '../style/app-theme.scss'

const appConfig = require('../app-config')

if (process.env.NODE_ENV !== 'production' && typeof AFRAME !== 'undefined') {
  const info = AFRAME.utils.debug(`${PROJECT_NAME}:info`)
  info('Looks like we are in development mode!')
  info(`Version: ${PROJECT_VERSION}-dev`)
  info(`report issues: ${PROJECT_ISSUE_URL}`)
  info(`A-Frame Version: ${AFRAME.version}`)
  info(`three.js Version: r${THREE.REVISION}`)
  info(`including TWEEN: ${TWEEN._nextId}`)
  console.dir(appConfig)
}

// ServiceWorker is a progressive technology. Ignore unsupported browsers
if ('serviceWorker' in navigator) {
  console.log('CLIENT: service worker registration in progress.')
  navigator.serviceWorker.register('/service-worker.js').then(function () {
    console.log('CLIENT: service worker registration complete.')
  }, function () {
    console.log('CLIENT: service worker registration failure.')
  })
} else {
  console.log('CLIENT: service worker is not supported.')
}

(function(){
  window.addEventListener("load", function load(event){
    var aScene = document.querySelector("a-scene");
    var landingDiv = document.querySelector("#background-landing");
    var progressDiv = document.querySelector("#progressDiv");
    var vrnBtn = document.querySelector("#enter-vr");
    var gearvrBtn = document.querySelector("#enter-gearvr");

    window.removeEventListener("load", load, false); //remove listener, no longer needed

    function onSceneLoaded(){

      if(progressDiv){
        progressDiv.remove();
      }

      if(vrnBtn){
        vrnBtn.style.display = "block";
        vrnBtn.addEventListener("click", entervrhandler);
      }

      if(gearvrBtn){
        gearvrBtn.style.display = "block";
        gearvrBtn.addEventListener("click", entergearvrhandler);
      }

      function entervrhandler() {
          landingDiv.remove();
          aScene.style.display = "block";
          playBackgroundMusic();
      }

      function entergearvrhandler() {
          landingDiv.display = "none";
          aScene.style.display = "block";
          document.querySelector('a-scene').enterVR();
      }


      function playBackgroundMusic() {
        //var music = document.querySelector('a-entity[sound]');
        var music = document.getElementById("music");
        music.components.sound.playSound();  
      }

      if( AFRAME.utils.device.isGearVR() ){
        console.log('AFRAME.utils.device.isGearVR ()');
        // trigger background music on entering VR on GearVR
        document.querySelector('a-scene').addEventListener('enter-vr', function () {
          playBackgroundMusic();
        });        

        document.addEventListener("GARREADY", function(e){
            e.detail.setCustomVariable('GearVR');
        });

      }  

      if ( AFRAME.utils.device.isMobile() ){
        console.log('AFRAME.utils.device.isMobile ()');
        document.addEventListener("GARREADY", function(e){
            e.detail.setCustomVariable('Cardboard / Mobile');
        });
      }  

      if( AFRAME.utils.device.isMobile() && AFRAME.utils.device.checkHeadsetConnected() ){
        console.log('AFRAME.utils.device.isMobile() && AFRAME.utils.device.checkHeadsetConnected()');
        document.addEventListener("GARREADY", function(e){
            e.detail.setCustomVariable('Oculus Rift / HTC Vive');
        });        
      }

      if( !AFRAME.utils.device.checkHeadsetConnected() ){
        console.log('Desktop');
        document.addEventListener("GARREADY", function(e){
            e.detail.setCustomVariable('Desktop');
        });        
      }


//////////////////////////// DEMOS
    var deviceDemo = document.getElementById("chimedevicedemo");
    var deviceDemoHide = document.getElementById("chimedevicedemohide");

    var capacityDemo = document.getElementById("chimecapacitydemo");
    var capacityDemoHide = document.getElementById("chimecapacitydemohide");

    var smartwanDemo = document.getElementById("smartwandemo");
    var smartwanDemoHide = document.getElementById("smartwandemohide");

    var skillsDemo = document.getElementById("contactcenterskillsdemo");
    var skillsDemoHide = document.getElementById("contactcenterskillsdemohide");

    var issuesDemo = document.getElementById("contactcenterissuesdemo");
    var issuesDemoHide = document.getElementById("contactcenterissuesdemohide");

    var retailDemo = document.getElementById("reachretaildemo");
    var retailDemoHide = document.getElementById("reachretaildemohide");

    var realestateDemo = document.getElementById("reachrealestatedemo");
    var realestateDemoHide = document.getElementById("reachrealestatedemohide");

    // var product_sip = document.getElementById("product_sip");
    // var product_crm = document.getElementById("product_crm");
    // var product_reach = document.getElementById("product_reach");
    // var product_swan = document.getElementById("product_swan");
    // var product_ccenter = document.getElementById("product_ccenter");
    // var product_chime = document.getElementById("product_chime");

      window.demochime1 = function () {
        deviceDemo.emit("chime-device-demo-play");          
//        capacityDemoHide.emit("chime-capacity-demo-hide");        
          ga('send', {
            hitType: 'event',
            eventCategory: 'Amazon Chime',
            eventAction: 'play',
            eventLabel: 'Demo - Access Across Devices'
          });
      }
      window.demochime2 = function () {
        capacityDemo.emit("chime-capacity-demo-play");
//        deviceDemoHide.emit("chime-device-demo-hide");                          
          ga('send', {
            hitType: 'event',
            eventCategory: 'Amazon Chime',
            eventAction: 'play',
            eventLabel: 'Demo - High Capacity Collaboration'
          });
      }
      window.bookmarkchime = function () {
          ga('send', {
            hitType: 'event',
            eventCategory: 'Amazon Chime',
            eventAction: 'check',
            eventLabel: 'Product Bookmarked'
          });
      }

      window.democontactcenter1 = function () {
//        issuesDemoHide.emit("contactcenter-issues-demo-hide");
          skillsDemo.emit("contactcenter-skills-demo-play");
          ga('send', {
            hitType: 'event',
            eventCategory: 'Contact Center',
            eventAction: 'play',
            eventLabel: 'Demo - ACD & Skill-based Routing'
          });
      }
      window.democontactcenter2 = function () {
//        skillsDemoHide.emit("contactcenter-skills-demo-hide");
          issuesDemo.emit("contactcenter-issues-demo-play");
          ga('send', {
            hitType: 'event',
            eventCategory: 'Contact Center',
            eventAction: 'play',
            eventLabel: 'Demo - Real-time Sentiment Analysis'
          });
      }
      window.bookmarkcontactcenter = function () {
          ga('send', {
            hitType: 'event',
            eventCategory: 'Contact Center',
            eventAction: 'check',
            eventLabel: 'Product Bookmarked'
          });
      }

      window.demosmartwan = function() {
          smartwanDemo.emit("smartwan-demo-play");          
          ga('send', {
            hitType: 'event',
            eventCategory: 'SmartWan',
            eventAction: 'play',
            eventLabel: 'Demo - Voice Prioritization/QoE'
          });
      }
      window.bookmarksmartwan = function () {
          ga('send', {
            hitType: 'event',
            eventCategory: 'SmartWan',
            eventAction: 'check',
            eventLabel: 'Product Bookmarked'
          });
      }

      window.demovonagereach1 = function () {
          retailDemo.emit("reach-retail-demo-play");
          ga('send', {
            hitType: 'event',
            eventCategory: 'Vonage Reach',
            eventAction: 'play',
            eventLabel: 'Demo - Case Study: Retail'
          });
      }
      window.demovonagereach2 = function () {
          realestateDemo.emit("reach-realestate-demo-play");
          ga('send', {
            hitType: 'event',
            eventCategory: 'Vonage Reach',
            eventAction: 'play',
            eventLabel: 'Demo - Case Study: Real Estate'
          });
      }
      window.bookmarkvonagereach = function () {
          ga('send', {
            hitType: 'event',
            eventCategory: 'Vonage Reach',
            eventAction: 'check',
            eventLabel: 'Product Bookmarked'
          });
      }

      window.videocrm = function () {
          ga('send', {
            hitType: 'event',
            eventCategory: 'CRM Integrations',
            eventAction: 'play',
            eventLabel: 'CRM Video Watched'
          });
      }
      window.bookmarkcrm = function () {
          ga('send', {
            hitType: 'event',
            eventCategory: 'CRM Integrations',
            eventAction: 'check',
            eventLabel: 'Product Bookmarked'
          });
      }
      
      window.videosip = function () {
          ga('send', {
            hitType: 'event',
            eventCategory: 'SIP Trunking',
            eventAction: 'play',
            eventLabel: 'SIP Trunking Video Watched'
          });
      }
      window.bookmarksip = function () {
          ga('send', {
            hitType: 'event',
            eventCategory: 'SIP Trunking',
            eventAction: 'check',
            eventLabel: 'Product Bookmarked'
          });
      }

      window.videoucaas = function () {
          ga('send', {
            hitType: 'event',
            eventCategory: 'UCaaS & CPaaS',
            eventAction: 'play',
            eventLabel: 'UCaaS & CPaaS Video Watched'
          });
      }
      window.bookmarkucaas = function () {
          ga('send', {
            hitType: 'event',
            eventCategory: 'UCaaS & CPaaS',
            eventAction: 'check',
            eventLabel: 'Product Bookmarked'
          });
      }

      window.opentrunk = function () {
          ga('send', {
            hitType: 'event',
            eventCategory: 'Tradeshow Only',
            eventAction: 'open',
            eventLabel: 'Treasure Trunk'
          });
      }

//////////////////////////// END DEMOS




    };
    if(aScene){
      (aScene.hasLoaded ? onSceneLoaded() : aScene.addEventListener("loaded", onSceneLoaded));
    }
  },false);
})();



